symEscape.addEventListener("click", () => {
  let sliceStr = opt.innerText;
  if (sliceStr.length === 1) {
    opt.innerText = 0;
  } else {
    opt.innerText = sliceStr.slice(0, -1);
  }
});
symEqual.addEventListener("click", () => {
  let str = opt.innerText;
  if (str.includes("+")) {
    const arr = str.split("+");
    add(Number(arr[0]), Number(arr[1]));
  }
  if (str.includes("-")) {
    const arr = str.split("-");
    subtract(Number(arr[0]), Number(arr[1]));
  }
  if (str.includes("*")) {
    const arr = str.split("*");
    multiply(Number(arr[0]), Number(arr[1]));
  }
  if (str.includes("÷")) {
    const arr = str.split("÷");
    divide(Number(arr[0]), Number(arr[1]));
  }
  if (str.includes("%")) {
    const arr = str.split("%");
    mod(Number(arr[0]), Number(arr[1]));
  }
  if (str.includes("√")) {
    const arr = str.split("√");
    console.log(arr[0]);
    if (arr[0] === "") {
      arr[0] = 1;
    }
    opSqrt(Number(arr[0]), Number(arr[1]));
  }
});
function add(num1, num2) {
  let result = num1 + num2;
  displayOutput(result);
}
function subtract(num1, num2) {
  let result = num1 - num2;
  displayOutput(result);
}
function multiply(num1, num2) {
  let result = num1 * num2;
  displayOutput(result);
}
function divide(num1, num2) {
  let result = num1 / num2;
  displayOutput(result);
}
function mod(num1, num2) {
  let result = num1 % num2;
  displayOutput(result);
}
function opSqrt(num1, num2) {
  let result = num1 * Math.sqrt(num2);
  displayOutput(result);
}
function clearOpt() {
  opt.innerText = 0;
}
function decimalCnt(num) {
  const numStr = String(num);
  if (numStr.includes(".")) {
    return numStr.split(".")[1].length;
  }
  return 0;
}
function displayOutput(result) {
  if (decimalCnt(result) > 3) {
    result = result.toFixed(3);
  }
  opt.innerHTML = `${opt.innerText} <p>=</p><div>${result}</div>`;
}
